@import url('https://fonts.googleapis.com/css2?family=Teko:wght@300;500;600&display=swap');

html{
  overflow: hidden;
  scroll-behavior: smooth;
}

*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  overflow-x: hidden;
  font-family: 'Teko', sans-serif;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/*#region NAVBAR*/
header{
  background-color: white;
  display: flex;
  justify-content: space-evenly;
  height: 10vh;
  align-items: center;
}

.nav-logo-container{
  display: grid;
  align-self: center;
  place-content: center;
}

.nav-logo{
  cursor: pointer;
}

.nav-header{
  align-self: center;
}

.nav-content{
  display: flex;
  justify-content: space-around;
  width: 30vw;
  align-self: center;
}

.nav-link{
  --b: 0.15em;   /* the thickness of the line */
  --c: #284c8c;  /* the color */
  
  color: #0000;
  padding-block: var(--b);
  background: 
    linear-gradient(var(--c) 50%,#000 0) 0% calc(100% - var(--_p,0%))/100% 200%,
    linear-gradient(var(--c) 0 0) 0% var(--_p,0%)/var(--_p,0%) var(--b) no-repeat;
  -webkit-background-clip: text,padding-box;
          background-clip: text,padding-box;
  transition: .21s var(--_s,0s) linear,background-size .3s calc(.3s - var(--_s,0s));

  font-weight: 700;
  font-size: 24px;
  cursor: pointer;
}

.nav-link:hover{
  --_p: 100%;
  --_s: .3s;
}

.nav-link a{
  text-decoration: none;
  color: #284c8c;
}

.nav-details{
  display: grid;
  align-content: center;
  padding-block: 0.6rem;
}

.i-moon{
  border: 3px solid #284c8c ;
  overflow: hidden;
  height: auto;
  font-size: 24px;
  padding: 5.1px;
  border-radius: 30%;
  cursor: pointer;
  margin-inline: 4.5rem;
  animation: iconRev 0.6s;
  
}

.i-moon:hover{
  animation: iconGo 0.6s forwards;
}
/*#endregion*/

/*#region MAINPAGE*/
.mainpage-box-principal{
  height: 90vh;
  overflow: hidden;
  position: relative;
}

.mainpage-container{
  width: inherit;
  height: inherit;
  display: flex;
  position: relative;
}

.mainpage-first-container{
  width: 100vw;
  align-self: center;
  margin-bottom: 12rem;
}

.mainpage-title1{
  overflow: hidden;
  font-size: 66px;
  font-weight: 900;
  text-align: center;
  color: #284c8c;
  animation: titleFloating 5s;
  animation-iteration-count: infinite;
}

.maintext-box{
  display: flex;
  justify-content: center;
  margin-top: 1.5rem;
}

.mainpage-text{
  width: 33%;
  font-size: 21px;
  text-align: center;
  color: gray;
  word-break: keep-all;
}
/*#endregion*/

/*#region STUDY*/
.study-page{
  overflow: hidden;
  height: 100vh;
  width: 100vw;
  position: relative;
}

.study-cardcontent{
  display: flex;
  justify-content: space-around;
  height: 90%;
}

.study-card{
  border: 2px solid black;
  border-radius: 15px;
  width: 21rem;
  height: 34.5rem;
  align-self: center;
  display: flex;
  flex-direction: column;
}

.study-cardimage{
  max-width: 100%;
  max-height: 40%;
  overflow: hidden;
  display: grid;
  place-content: center;
  margin: 1rem;
}

.study-third-image{
  width: 100%;
  height: max-content;
  object-fit: cover;
  border-radius: 1rem;
}

.study-textcontent{
  height: 60%;
  display: flex;
  flex-wrap: wrap;
}

.study-card-title{
  margin-inline: 1rem;
  margin-block: 0.3rem;
  font-size: xx-large;
  letter-spacing: 3px;
}

.study-card-text{
  margin-inline: 1rem;
  word-break: keep-all;
  text-align: justify;
  font-size: 18px;
}

.study-card-button{
  width: 100%;
  background-color: #fff;
  color: #000;
  border: 2px solid #000;
  cursor: pointer;
  font-weight: 600;
  text-align: center;
  text-decoration: none;
  outline: 0;
  letter-spacing: .8px;
  height: 48px;
  min-width: 140px;
  touch-action: manipulation;
  margin: 1.2rem;
  align-self: flex-end;
}

.study-card-button:hover{
  border-color: #284c8c;
  color: #284c8c;
  fill: #284c8c;
  transform: translateY(-2px);
  box-shadow: rgba(0, 0, 0, 0.25) 0 8px 15px;
}

.study-cardmodal-hidden{
  display: none;
}

.study-cardmodal{
  display: block;
  position: absolute;
  top: 3.3rem;
  border: 2px solid black;
  border-radius: 15px;
  width: 70vw;
  height: fit-content;
  z-index: 99;
  background: white;
  box-shadow: 0px 0px 2000px 1000px rgba(50,50,50,0.75);
}

.study-modalcontent{
  height: fit-content;
  width: fit-content;
  display: flex;
  padding-block: 0.6rem;
  padding-inline: 1rem;
  margin-top: 0.6rem;
}

.scm-back{
  padding: 0.3rem;
  padding-left: 1rem;
  padding-bottom: 0.9rem;
  cursor: pointer;
  align-self: center;
  font-size: 33px;
}

.scm-title{
  align-self: center;
  font-size: 48px;
  padding-left: 3rem;
  letter-spacing: 6px;
}

.scm-maincontent{
  height: fit-content;
  width: fit-content;
  padding-block: 3rem;
  margin-inline: 1.8rem;
  border-top: 3px solid #284c8c;
}

.scm-maintext{
  color: #284c8c;
  font-size: 18.6px;
}
/*#endregion*/

/*#region PROJECT*/
.project-page{
  overflow: hidden;
  height: 100vh;
  width: 100vw;
  display: grid;
  place-items: center;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, 1fr);
}

.project-card{
  height: 24vh;
  width: 27vw;
  border: 3px solid black;
  border-radius: 12px;
  cursor: pointer;
  box-shadow: 6px 6px 21px 3px rgba(102,102,102,0.8);
  position: relative;
}

.pc-imgcontainer{
  display: flex;
  height: 100%;
}

.pc-image{
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.pc-titlecard-bottom{
  position: absolute;
  top: 66%;
  left: 6%;
  color: whitesmoke;
  font-size: 39px;
  -webkit-text-stroke: 1.8px #000;
  text-overflow:initial;
}

.pc-titlecard-top{
  position: absolute;
  top: 6%;
  left: 6%;
  color: whitesmoke;
  font-size: 39px;
  -webkit-text-stroke: 1.8px #000;
  text-overflow:initial;
}
/*#endregion*/

/*#region CONTACT*/
.contact-page{
  overflow: hidden;
  height: 100vh;
  width: 100vw;
}

.contact-card{
  height: 70vh;
}

.contact-social{
  height: 20vh;
  margin-inline: 2.1rem;
  border-top: 3px solid #284c8c;
  display: flex;
  display: flex;
  justify-content: space-evenly;
}

.bx{
  font-size: 90px;
  align-self: center;
  padding-inline: 2.1rem;
  cursor: pointer;
}

.bxl-linkedin-square:hover{
  color: #0e76a8;
  transform: translateY(-3px);
}

.bxl-github:hover{
  color: #171515;
  transform: translateY(-3px);
}

.bxl-instagram:hover{
  background: radial-gradient(circle at 30% 107%, #fdf497 0%, #fdf497 5%, #fd5949 45%,#d6249f 60%,#285AEB 90%);
  transform: translateY(-3px);
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.bxl-gmail:hover{
  color: #D44638;
  transform: translateY(-3px);
}

.bxs-user-rectangle:hover{
  color: #284c8c;
  transform: translateY(-3px);
}

.icons-a{
  align-self: center;
  color: black;
  text-decoration: none;
  height: fit-content;
  width: fit-content;
}

.contact-card{
  display: grid;
  place-items: center;
  position: relative;
}

.contact-form{
  display: flex;
  flex-direction: column;
  padding-inline: 4.5rem;
  padding-block: 1.5rem;
}

.contact-form-title{
  color: #284c8c;
  letter-spacing: 9px;
  font-size: 45px;
  width: 30vw;
  text-align: center;
}

.contact-form label{
  margin-top: 0.6rem;
  font-size: 18px;
  color: #284c8c;
}

.contact-form input{
  border: 1px solid #58585850;
  font-size: 15px;
  padding-block: 2.4px;
  border-radius: 6px;
  letter-spacing: 1.5px;
  padding-inline: 3px;
}

.contact-form textarea{
  resize: none;
  border: 1px solid #58585850;
  font-size: 15px;
  padding-block: 2.4px;
  border-radius: 6px;
  letter-spacing: 1.5px;
  padding-inline: 3px;
}

.contact-form-button{
  width: 100%;
  background-color: #fff;
  color: #000;
  border: 2px solid #000;
  cursor: pointer;
  font-weight: 600;
  text-align: center;
  text-decoration: none;
  outline: 0;
  letter-spacing: .8px;
  height: 48px;
  min-width: 140px;
  touch-action: manipulation;
  margin-top: 1.5rem;
  align-self: flex-end;
}

.contact-form-button:hover{
  border-color: #284c8c;
  color: #284c8c;
  fill: #284c8c;
  transform: translateY(-2px);
  box-shadow: rgba(0, 0, 0, 0.25) 0 8px 15px;
}
/*#endregion*/

/*#region PAGE404 */
.page404{
  display: grid;
  height: 100vh;
  place-items: center;
}

.page404-text{
  text-align: center;
}

.page404-page{
  font-size: 60px;
  color: #fff;
  word-spacing: 9px;
  -webkit-text-stroke: 3px #284c8c;
  margin-bottom: 9rem;
}

.page404-404{
  font-size: 123px;
  color: #fff;
  letter-spacing: 9px;
  -webkit-text-stroke: 6px #284c8c;
}
/*#endregion*/

@media (max-width: 800px){
  html{
    overflow-y: scroll;
  }

  /*#region NAVBAR*/
  header{
    display: flex;
    flex-direction: column;
    height: 12rem;
    position: relative;
  }

  .nav-logo{
    width: 180px;
  }

  .nav-header{
    width: 100vw;
  }

  .nav-content{
    width: 100%;
    display: flex;
    justify-content: space-around;
  }

  .nav-details{
    position: absolute;
    top: 2.7rem;
    right: 0rem;
  }

  .i-moon{
    margin-inline: 2.4rem;
  }
  /*#endregion*/

  /*#region MAINPAGE*/
  .mainpage-first-container{
    width: 100vw;
    align-self: center;
    margin-bottom: 16.5rem;
  }
  
  .mainpage-title1{
    overflow: hidden;
    font-size: 42px;
    font-weight: 900;
    text-align: center;
    color: #284c8c;
    animation: titleFloating 5s;
    animation-iteration-count: infinite;
  }

  .mainpage-text{
    width: 46.5%;
    font-size: 21px;
    text-align: center;
    color: gray;
    word-break: keep-all;
  }
  /*#endregion*/

  /*#region STUDYPAGE*/
  .study-page{
    overflow-y: scroll;
    display: flex;
    justify-content: center;
    height: fit-content;
  }
  
  .study-cardcontent{
    display: flex;
    flex-direction: column;
    width: 93vw;
    margin-top: 0;
    height: auto;
    align-self: flex-start;
  }

  .study-card{
    height: fit-content;
    width: 100%;
    margin-bottom: 1.8rem;
  }

  .pagination-content{
    margin-bottom: 4rem;  
  }

  .pagination-study{
    margin-bottom: 2rem;
    display: flex;
    justify-content: center;
    
  }

  .pagination-page{
    font-size: x-large;
    margin-inline: 6px;
    border: 1px solid #284c8c;
    padding-inline: 13.5px;
    padding-block: 3px;
  }

  .pagination-page-active{
    font-size: x-large;
    margin-inline: 6px;
    border: 1px solid #284c8c;
    padding-inline: 13.5px;
    padding-block: 3px;
    color: #fff;
    background-color: #284c8c;
  }
  /*#endregion*/

  /*#region PROJECT*/
  .project-page{
    height: fit-content;
    width: 100vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
  }

  .project-card{
    height: 30vh;
    width: 84vw;
    border: 3px solid black;
    border-radius: 12px;
    cursor: pointer;
    box-shadow: 6px 6px 21px 3px rgba(102,102,102,0.8);
    margin-block: 1.8rem;
    position: relative;
  }

  .pc-image{
    width: 100%;
    object-fit: cover;
  }

  .pc-titlecard{
    position: absolute;
    color: whitesmoke;
  }
  /*endregion */

  /*#region CONTACTPAGE*/
  .contact-page{
    height: 84vh;
    width: 100vw;
  }

  .contact-form{
    display: flex;
    flex-direction: column;
    padding-inline: 1.5rem;
    padding-top: 0.6rem;
    padding-bottom: 1.5rem;
  }

  .contact-form-title{
    color: #284c8c;
    letter-spacing: 6px;
    font-size: 45px;
    width: 75vw;
    text-align: center;
  }

  .contact-social{
    height: 15vh;
    margin-inline: 1.2rem;
    border-top: 3px solid #284c8c;
    display: flex;
    justify-content: space-evenly;
  }

  .bx{
    font-size: 42px;
    align-self: center;
    padding-inline: .6rem;
  }
  /*#endregion */
}

  /*#region ANIMATIONS*/
@keyframes iconGo{
  from {
    transform: rotate(0deg);
  }
  to {
    border-radius: 50%;
    transform: rotate(360deg);
  }
}

@keyframes iconRev{
  from {
    border-radius: 50%;
    transform: rotate(360deg);
  }
  to {
    transform: rotate(0deg);
  }
}

@keyframes titleFloating {
  0% {
    transform: translateY(-3px);
  }
  50% {
    transform: translateY(7px);
  }
  100% {
    transform: translateY(-3px);
  }
}
  /*#endregion*/